body {
  --moyo-blue: #00acdc;
  background-color: hwb(194% 64% 22%);
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.img-container {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  align-items: center;
  display: flex;
}

.logo {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  white-space: nowrap;
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--moyo-blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--moyo-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
